.about__container {
  display: grid;
  grid-template-columns: 35% 35%;
  gap: 15%;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  transition: var(--transition);
  width: 50%;
}

.about__me-image:hover {
  background: transparent;
  background-color: rgba(194, 114, 114, 0.5); 
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
  
}

.about__card {
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  border: .5px solid transparent;
  border-radius: 1rem;
  padding: 1.5rem;
  text-align: center;
  transition: var(--transition);
  
}

.about__card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
  background-color: rgba(244, 116, 116, 0.5); 
}

.about__icon {
  color:rgb(1, 1, 1);
  font-size: 3.4rem;
  margin-bottom: 1rem;
}

.about__card h5 {
  font-size: 1.25rem;
}

.about__card small {
  font-size: 1.25rem;
  color: rgb(1, 1, 1);
}

.about__content p {
  margin: 4rem -5rem 2.6rem;
  color: rgb(6, 178, 246);
  font-size: larger;
}


/* ========== MEDIA QUERIES (MEDIUM DEVICES) ========== */
@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about__content p {
    margin: 1rem 0 1.5rem;
  }
}

/* ========== MEDIA QUERIES (SMALL DEVICES) ========== */
@media screen and (max-width: 600px) {
  .about__me {
    width: 30%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1rem;
  }

  .about__content {
    text-align: center;
  }

  .about__content p {
    margin: 1.5rem 0;
  }
}